	padding: 10rem 0;
}
.consultation h2 {
	margin-bottom: 8rem;
}
.consultation h2,
.consultation .p-heading {
	text-align: center;
}
.consultation .input-wrapper {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}
.consultation input {
	width: 30%;
	padding: 1.5rem 4rem;
	border: none;
	border-radius: 50px;
	margin-bottom: 3rem;
}
.consultation input::placeholder {
	color: black;
	font-family: "Circe Bold", sans-serif;
}
.consultation .input-text {
	display: block;
	width: 100%;
}
.consultation .button-order {
	margin-top: 1rem;
}
.footer {
	padding: 4rem 0 3rem 0;
}
.footer th {
	padding: 0.5rem;
	padding-bottom: 2.5rem;
	font-family: "Circe Extra Bold", sans-serif;
	font-size: 1.6rem;
}
.footer td {
	padding: 0.5rem;
	padding-right: 6rem;
	font-size: 1.3rem;
}
.footer p {
	text-align: right;
	color: #000;
	font-family: "Circe Bold", sans-serif;
}
.footer p a {
	color: inherit;
	text-decoration: underline;
}
.footer .phone {
	font-size: 2.6rem;
	font-family: "Circe Extra Bold", sans-serif;
}
.footer .phone-whatsapp {
	font-size: 1.8rem;
	margin: 1rem 0;
}
.footer .modernwebdev {
	margin-top: 1rem;
}

.popup-consultation {
	z-index: 20;
	position: fixed;
	display: none;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(178, 80, 188, 0.8);
}

.popup-consultation .button-transparent {
	border: 2px dashed #c51abb;
	width: 100%;
	margin-bottom: 2rem;
}
.popup-consultation .form {
	margin: 0;
	padding-top: 0;
	padding-bottom: 0;
}
.popup-consultation .form input,
.popup-consultation .form textarea {
	border: none;
