	margin-bottom: 2.5rem;
	width: 100%;
	color: inherit;
	resize: none;
}
.popup-consultation .form input::placeholder,
.popup-consultation .form textarea::placeholder {
	color: black;
	text-align: center;
	font-family: "Circe Bold", sans-serif;
}
.popup-consultation .button-order {
	width: 100%;
	margin-top: 1rem;
}
.popup-consultation h4 {
	margin-bottom: 3.5rem;
}
.popup-close {
	position: absolute;
	top: -5rem;
	right: -1.5rem;
	font-size: 5rem;
	width: 5rem;
	height: 5rem;
	line-height: 5rem;
	text-align: center;
	background: transparent;
	color: #ffffff;
	border: none;
}
.popup-content {
