.popup-design input[name="name"] {
	margin-top: 2rem;
}
.popup-design .file_upload {
	position: relative;
	overflow: hidden;
	font-size: 1em;
	/* example */

	height: 4em;
	/* example */

	line-height: 2em /* the same as height */;
}
.popup-design .file_upload > button {
	float: right;
	width: 8em;
	/* example */

	height: 100%;
}
.popup-design .file_upload > div {
	padding-left: 1em;
	/* example */
}
.popup-design .file_upload input[type="file"] {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	transform: scale(20);
	letter-spacing: 10em;
	/* IE 9 fix */

	-ms-transform: scale(20);
	/* IE 9 fix */

	opacity: 0;
	cursor: pointer;
}
.popup-design .file_upload > button {
	transition: background 0.2s;
	border-radius: 50px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	background-color: transparent;
	font-size: 1.5rem;
	color: #c51abb;
	border: 2px dashed #c51abb;
	padding: 1.5rem;
}
.popup-design .file_upload > button:before {
	content: "";
	background: url("../img/camera.svg") no-repeat center;
	width: 1.6rem;
	height: 1.6rem;
	display: inline-block;
	position: relative;
	top: 3px;
	right: 2px;
	background-size: 100%;
}
