/*scroll*/
*::-webkit-scrollbar {
	width: 3px;
}

*::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

*::-webkit-scrollbar-thumb {
	background-color: darkgrey;
	outline: 1px solid slategrey;
}

.pageup {
	opacity: 0;
	width: 26px;
	height: 26px;
	position: fixed;
	bottom: 100px;
	right: 5%;
	z-index: 30;
	background-color: #fff;
	cursor: pointer;
	border: 1px solid white;
	transition: 1s all;
}

